/* GLOBAL */
:root {
  /* Primary */
  --white: #ffffff;
  --night: #232740; /* Text */
  --sunrise: #ff6962; /* Primary call to action color */
  /* Secondary */
  --dusk: #48527b; /* "Highlight" background color */
  --sky: #48c1fc; /* Secondary call to action color */
  /* Tertiary */
  --cloud: #e7e7ec; /* For borders, backgrounds */
  --sand: #f9f5f0; /* For subtle micro-interactions */
}

body {
  color: var(--night);
}

.App {
  /* position: fixed; */
  font-family: 'Open Sans', sans-serif;
  text-align: center;
  width: 100vw;
  height: 100vh;
}

.content {
  position: absolute;
  top: 70px;
  width: 100%;
  height: 100%;
}

.caption {
  font-size: 12px;
}

.caption-strong {
  font-size: 12px;
  font-weight: bold;
}

h2 {
  color: #1f3349;
  margin-top: 12px;
  font-size: 20px;
}

h3 {
  font-weight: normal;
}

p {
  font-size: 14px;
  line-height: 1;
}

.attribute {
  color: #1f3349;
  font-size: 14px;
  font-weight: bold;
  margin: 0;
}

/* MAP */
.mapContainer {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

.sidebarStyle {
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  margin: 12px;
  background-color: #48527b;
  color: #fff;
  z-index: 1 !important;
  padding: 6px;
  font-weight: bold;
  border-radius: 20px;
}

.popUp {
  display: inline-block;
  position: absolute;
  top: 0;
  right: 0;
  margin: 2px 60px;
  background-color: #48527b;
  color: white;
  z-index: 1 !important;
  padding: 3px 10px;
  font-weight: bold;
  border-radius: 15px;
}

.popUp :after {
  content: '\22B3';
}

/* NAV BAR */
nav {
  width: 100vw;
  height: 70px;
  background-color: #8be1dc;
  z-index: 1;
  position: fixed;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.logo {
  width: 33%;
  font-weight: bold;
  font-size: 18px;
}

.account-links {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  width: 33%;
  padding-right: 32px;
}

.account-links a {
  padding-left: 16px;
  text-decoration: none;
}

/* TRIPS PAGE */
.wrapper {
  width: 100%;
  height: 100%;
  position: fixed;
  margin: 32px auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}

.filter-wrapper {
  width: 35%;
  height: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
}

.trips-wrapper {
  width: 65%;
  height: 100%;
  position: absolute;
  bottom: 0;
  right: 0;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

/* SEARCH & FILTER PANEL */
.filter-panel {
  width: 60%;
  height: 75%;
  padding: 24px 32px;
  background-color: var(--cloud);
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

.search-filters {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: flex-start;
  text-align: left;
}

.filter-panel-button {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  margin-left: -8px;
}

/* .filter-panel div {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
} */

.filter-panel label {
  color: var(--night);
  display: block;
  text-align: left;
  margin: 6px;
  font-size: 14px;
}

.checkbox {
  margin-right: 12px;
}

.search-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid var(--white);
  padding-bottom: 24px;
  margin-bottom: 24px;
}

.search-input {
  height: 40px;
  width: 100%;
  font-size: 14px;
  border: none;
  border-radius: 6px;
  text-indent: 12px;
}

.search-button {
  height: 44px;
  width: 100%;
  border-radius: 6px;
  background-color: var(--sunrise);
  color: #fff;
  font-size: 14px;
  font-weight: bold;
  margin: 8px;
  border: none;
}

/* TRIP CARD */
.trip-card {
  width: 85%;
  height: auto;
  padding: 18px;
  margin-bottom: 24px;
  border-radius: 12px;
  border: 1px solid var(--cloud);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  -webkit-box-shadow: 7px 6px 36px -15px rgba(109, 109, 151, 0.4);
  -moz-box-shadow: 7px 6px 36px -15px rgba(109, 109, 151, 0.4);
  box-shadow: 7px 6px 36px -15px rgba(109, 109, 151, 0.4);
}

.trip-card a {
  text-decoration: none;
  color: var(--night);
}

.trip-review-container {
  width: 200px;
  height: 200px;
}

.primary-content {
  text-align: left;
  line-height: 0.6;
  margin-bottom: 16px;
}

.secondary-content {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-bottom: 12px;
}

.secondary-content div {
  width: 25%;
  line-height: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  text-align: left;
}

.trips-list {
  width: 100%;
  margin-bottom: 150px;
}

/* SIGNUP / LOGIN */

.auth-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-top: 10%;
}

.auth-box {
  width: 300px;
  height: auto;
  padding: 18px;
  border-radius: 14px;
  border: 1px solid var(--cloud);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  -webkit-box-shadow: 7px 6px 36px -15px rgba(109, 109, 151, 0.4);
  -moz-box-shadow: 7px 6px 36px -15px rgba(109, 109, 151, 0.4);
  box-shadow: 7px 6px 36px -15px rgba(109, 109, 151, 0.4);
}

.auth-form {
  display: flex;
  flex-direction: column;
}

.auth-form div {
  width: 250px;
  height: 35px;
  padding: 10px;
}

.auth-form a {
  text-decoration: none;
}

.auth-form input {
  width: 100%;
  height: 100%;
  border: 1px solid var(--cloud);
  border-radius: 7px;
  font-size: 16px;
  text-align: center;
  padding: 0;
}

.auth-btn {
  width: 100%;
  height: 100%;
  border: 1px solid var(--cloud);
  border-radius: 7px;
  font-size: 16px;
  color: var(--night);
  background-color: var(--sunrise);
  padding: 0;
}

.google-btn {
  background-color: var(--sky);
}

