.home-container {
  background-image: url('https://github.com/timbotimber/cyclingproject/blob/master/server-code/client/public/img/bike4.jpeg?raw=true');
  height: 70vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.phone {
  height: 600px;
}

.main {
  height: 50%;
  width: 40%;
  margin-left: 7%;
  margin-top: 5%;
  margin-bottom: 5%;
  color: #1f3349;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 1%;
  border-radius: 7px;
}

.mainphone {
  height: 50%;
  width: 40%;
  margin-left: 7%;
  margin-top: 2%;
  color: #1f3349;
  padding: 1%;
  border-radius: 7px;
}

.main h1 {
  font-size: 34px;
  font-weight: 300;
}

.main span {
  font-weight: bolder;
}

.intro {
  text-align: left;
}

.intro p {
  font-size: 24px;
}

.home-btns-wrapper {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-top: 6%;
  height: 10%;
}

.home-btn {
  width: 230px;
  height: 45px;
  border: 1px solid var(--cloud);
  border-radius: 7px;
  font-size: 18px;
  color: #f5f0f0;
  background-color: var(--sunrise);
  padding: 0;
  margin: 0;
}

.home-btns a {
  text-decoration: none;
}

